import '../css/my-account.scss';

import OrderHistoryController from 'orders/OrderHistoryController';
import OrderItemFullDetailController from 'orders/orderItemFullDetailController';

/**
 * Initialize DOM for Page
 */
var Page = (function () {

    /** helpers **/

    // expose this object to a namespace
    return {

        nodes: {},

        onDOMContentLoaded: function () {
            // fixed navigation
            var nodes = this.nodes,
                self = this,
                $form = $( '#aspnetForm' );

            $form.validate();
        },

        init: function () {
            var ns = upi.namespace( 'Pages.MyAccount' );
            upi.Pages.MyAccount = this;

            $( this.onDOMContentLoaded );

            var self = this;
            this.nodes.$page = $( '#s4-workspace' );
            this.nodes.$form = $( '#aspnetForm' );
            this.nodes.$siteContent = $( '#page-content' );
            this.nodes.$mainContent = $( '#body-content' );
            this.nodes.$leftNav = $( '#navigationBeta' );

            var nodes = this.nodes;

            new OrderHistoryController( '#odr-history-container' );

            // exclude pending orders
            var trackedOrders = $( '[data-order-status]' ).filter( function () {
                return !/pending/.test( this.getAttribute( 'data-order-status' ) );
            } );
            if ( trackedOrders.length ) {
                this.getOrderDetails( trackedOrders );
            }

        },

        getOrderDetails: function ( trackedOrders ) {
            var nodes = this.nodes;

            // initialize Order Detail module
            nodes.$siteContent.corpus( 'tabs', {
                controls: '.tabNavigation',
                panel: '.tabPanel',
                active_class: 'current'
            } );

            trackedOrders.each( function () {
                new OrderItemFullDetailController( this );
            } );
        }
    };
}());

$( () => Page.init() );



