import Class from '../core/Class';
import orderUtil from 'orders/orderUtil';
import OrderItemFullDetailController from 'orders/orderItemFullDetailController';
import OrdersService from '../../services/OrdersService';
import sortBy from 'lodash.sortby';
import filter from 'lodash.filter';
import Module from '../core/Module';
import View from '../core/View';
import SelectMenu from '../ui/SelectMenu';
import User from '../User';

const conf = {
        orderHistoryRange: {
            numberOfDays: 365,
            resultsLimit: 100
        },
        filterOptions: {
            90: 'Past 90 days',
            182: 'Past 6 months',
            365: 'Past 12 months',
            546: 'Past 18 months'
        },
        messages: {
            ORDER_HISTORY_PROCESSING: 'Retrieving orders...',
            ORDER_HISTORY_FAILED: 'Sorry, there was a problem on our end and we were unable to retrieve your order information',
            ORDER_HISTORY_PROMISE_REJECT: 'Request for order history list failed',
            ORDER_HISTORY_MISSING_PARAMS: 'getOrderHistoryFor: missing required keys to get order info '
        }
    },
    getNumberOfDaysFromNow = upi.Date.getNumberOfDaysFromNow;

var OrderHistoryController = (Class.extend( Module )).extend( {

    defaults: {
        request: OrdersService.getOrderHistory
    },

    events: {},

    elements: {
        '.odr-history-actions': 'orderActionsContentArea',
        '.odr-list.accordion': 'contentArea'
    },

    init: function ( el, options ) {
        this.options = this.options || {};
        Object.extend( this.options, this.defaults, options || {} );

        this.orderSelect = null;
        this.el = $( el );

        if ( !this.el[0] ) {
            return;
        }

        this.messageBoxView = new View( 'messagebox' );
        this.update_elements();

        this.messageBoxView.whenTemplatesLoaded.then( () => {
            this.orders = this.getOrderHistory()
                .then( this.renderOrderHistory.bind( this ) )

        } )
    },

    destroy: function () {
        this.el.trigger( 'destroy:accordion' );
    },

    redirectToLogin: function () {
        this.setLoadMessage( 'redirecting', upi.dialog.SESSION_TIMEOUT );
        window.setTimeout( User.redirectToLogin, 750 );
    },

    setLoadMessage: function ( type, message ) {
        var messageView = this.messageBoxView.render( {
            // these message bars will always use the slideDown animation
            type: (type || '') + '',
            effect: 'slideDown',
            message: message || ''
        } );

        this.contentArea.html( messageView );
    },

    toggleEmptyMessage: function ( doShow ) {
        this.el.toggleClass( 'is-empty', !!doShow );
    },

    getOrderHistory: function () {
        const {request} = this.options;

        if ( !User.isLogged() ) {
            User.redirectToLogin();
            return Promise.reject();
        }

        this.toggleEmptyMessage( false );
        this.setLoadMessage( 'processing', conf.messages.ORDER_HISTORY_PROCESSING );

        if ( typeof request !== 'function' ) {
            return Promise.reject( conf.messages.ORDER_HISTORY_MISSING_PARAMS + '"request" option' );
        }
        else {
            return request( conf.orderHistoryRange ).catch(
                error => {
                    let reason = error.reason;

                    switch ( reason ) {
                        // not authenticated
                        case 'noauth':
                            this.redirectToLogin();
                            break;

                        default:
                            this._handleUnknownError();
                    }

                    return Promise.reject();
                }
            );
        }
    },

    showOrdersForDayRange: function ( numDays ) {
        var self = this,
            filteredOrders;

        this.orders.then( function ( data ) {
            var sequence = [];

            filteredOrders =
                filterOrdersByNumberDays( data.Orders, numDays ).map( function ( {ConfirmationOrderNumber} ) {
                    return ConfirmationOrderNumber && ConfirmationOrderNumber.toString();
                } );

            self.orderList.each( function ( i, item ) {
                var $item = $( item ),
                    itemId = item.getAttribute( 'data-order-number' ),
                    doShow = filteredOrders.indexOf( itemId.toString() ) > -1;

                sequence.push( {el: $item, doShow: doShow} );
            } );

            var count = 0;

            function toggleItem ( item ) {
                count++;
                item.el.toggleClass( 'show', item.doShow );

                setTimeout( function () {
                    if ( count < sequence.length ) {
                        toggleItem( sequence[count] );
                    }
                }, 30 );
            }

            if ( sequence.length ) {
                toggleItem( sequence[0] );
            }
        } );
    },

    renderOrderHistory: function ( data ) {
        // build the template
        var filterOptions;

        if ( data.Orders && data.Orders.length >= 1 ) {
            if ( this.orderSelect ) {
                this.orderSelect.destroy();
            }

            filterOptions = getOrderDateFilterOptions(
                data.Orders,
                conf.filterOptions
            );

            // store ref to <select> to handle cleanup for re-renders
            this.orderSelect = createPastOrderSelect( filterOptions, this.showOrdersForDayRange.bind( this ) );

            createOrderListHtml( sortOrdersByDate( data.Orders ) ).then( orderListHtml => {
                this.orderList = orderListHtml;
                this.showOrdersForDayRange( this.orderSelect.selectedOpt.value );

                this.contentArea.html( this.orderList );
                this.orderActionsContentArea.html( this.orderSelect.selEl );

                // initialize OrderItemController for each item in order history
                // order items must be in the DOM before initializing Controller
                this.orderList.each( function ( i, el ) {
                    new OrderItemFullDetailController( el );
                } );
            } );

        }
        else {
            this.toggleEmptyMessage( true );
            this.contentArea.empty();
        }

        return data;
    },

    _handleUnknownError: function () {
        // todo: attempt 2-3 times, then fail gracefully
        this.setLoadMessage( 'error', conf.messages.ORDER_HISTORY_FAILED );
    }

} );

window.OrderHistoryController = OrderHistoryController;
export default OrderHistoryController;

/****************************
 *  helpers
 *****************************/
/**
 * Format ViewModel to be passde to OrderHistoryItem template
 * @param {Object} order
 * @returns {{orderDate: *, PONumber: (*|PONumber), confirmationOrderNumber: *, shipAddressId: *, orderStatus: *}}
 */
function serializeOrderHistoryItem ( order ) {
    function getTrackingAvailability ( order ) {
        // hide tracking tab 90 days after order was placed
        // hide tracking tab if order status: pending, cancelled
        return (
            getNumberOfDaysFromNow( order.OrderDate ) < 90 &&
            !/pending|cancelled/gi.test( order.OrderStatus )
        );
    }

    return {
        orderDate: upi.Date.format( order.OrderDate, 'longNameFull' ),
        PONumber: order.PONumber,
        confirmationOrderNumber: order.ConfirmationOrderNumber,
        shipAddressId: order.ShipAddressID,
        orderStatus: order.OrderStatus.toLowerCase(), // use mapping
        orderStatusBadgeHtml: orderUtil.renderStatusBadge( order.OrderStatus ),
        // buildUrl -> /orderDetails.aspx?ordernumber=123&shipaddressid=123
        orderDetailsUrl: `${upi.paths.STORE_URL}/orderDetails.aspx?ordernumber=${order.ConfirmationOrderNumber}&shipaddressid=${order.ShipAddressID}`,
        showTrackingInfo: getTrackingAvailability( order )
    };
}

function createPastOrderSelect ( options, fn ) {
    fn = fn || upi.noop;

    console.log( 'options:', options );
    var el = createSelect( options );
    var select = new SelectMenu( el, {onChange: fn} );

    return select;
}

/**
 *
 * @param {Array} options - array of option objects containing <option> label and value
 * @returns {HTMLSelectElement}
 */
function createSelect ( options ) {
    var optionHtml = [],
        defaultSet = false,
        select = $( '<select class="cs-select action-control odr-history-actions--select-past">' );

    for ( var key in options ) {
        if ( options.hasOwnProperty( key ) ) {
            optionHtml.push( '<option ' + (defaultSet
                ? ''
                : 'selected') + ' value="' + key + '">' + options[key] + '</option>' );
            if ( !defaultSet ) {
                defaultSet = true;
            }
        }
    }

    return select.html( optionHtml.join( '' ) )[0];
}

function createOrderListHtml ( orders ) {
    var listHtml,
        tmpl = new View( 'orderHistoryItem' );

    return tmpl.whenTemplatesLoaded.then( () => {

        listHtml = orders.map( function ( orderItem ) {
            return tmpl.render( serializeOrderHistoryItem( orderItem ) );
        } );

        return $( listHtml.join( '' ) );
    } );
}

/**
 * Sort a list of orders by date defaulting to descending order
 * @param {Array} orders - an array of Order objects
 * @param dir - direction to sort [descending(default), ascending]
 * @returns {Array} orders
 */
function sortOrdersByDate ( orders, dir ) {
    var sortedOrders = sortBy( orders, function ( item ) {
        return new Date( item.OrderDate );
    } );

    if ( dir !== 'ascending' ) {
        sortedOrders.reverse();
    }

    return sortedOrders;
}

/**
 * Return a filtered list of Order objects starting from a specified number of days and beyond
 * @param {Array} orders - an array of Order objects
 * @returns {Array} orders
 */
function filterOrdersByNumberDays ( orders, numDaysLimit ) {
    return filter( orders, function ( order ) {
        return getNumberOfDaysFromNow( order.OrderDate ) < numDaysLimit;
    } );
}

/**
 * Get the available sort date options for the current order list.
 * Finds the most recent order in the list and uses the order date to determine the baseline
 * for the available options starting at 90days, 180days, 365days
 * @param {Array} orders - an array of Order objects
 */
function getOrderDateFilterOptions ( orders, optionMap ) {
    optionMap = optionMap || {};

    var mostRecentOrder = sortOrdersByDate( orders )[0],
        filteredOptionMap = {},
        daysFromNow = getNumberOfDaysFromNow( mostRecentOrder.OrderDate );

    console.log( 'meta:', orders, daysFromNow, mostRecentOrder.OrderDate );
    for ( var key in optionMap ) {
        if ( optionMap.hasOwnProperty( key ) && Math.min( daysFromNow, 546 ) <= key ) {
            filteredOptionMap[key] = optionMap[key];
        }
    }

    console.log( 'FILTERS:', optionMap );
    return filteredOptionMap;
}



